@import '../../theme_variables';

// lnk on detail product page
#wishlist_button,
#wishlist_button_nopop {
  font-weight: 700;
  background-color: transparent;
  border-color: transparent;
  margin: auto;
  outline: 0;
  cursor: pointer;

  &::before {
    content: '\f08a';
    font: 400 15px/31px $font-icon;
    color: $light-text-color;
    background: #929292;
    width: 30px;
    text-align: center;
    display: inline-block;
    height: 30px;
    margin: 0 6px;
  }
}

// wishlist block
#wishlist_block {

  #wishlist_block_list {
    margin: 0 0 20px;

    dl {
      font-weight: 400;

      &.products.no-products {
        font-weight: 700;
        padding: 0 0 15px;
        border-bottom: 1px solid $base-border-color;

        dt {
          padding: 0;
          border-top: 0;
        }

        dd {
          display: none;
        }
      }

      &.products {
        border-bottom: 1px solid $base-border-color;

        dt {
          padding: 20px 0 0;
          border-top: 1px solid $base-border-color;
        }

        dd {
          padding: 0 0 20px;
        }

        dt.first_item {
          padding: 0;
          border-top: 0;
        }
      }

      dt {
        position: relative;
        padding-right: 0;
      }

      dd {
        margin: 0 0 0 24px;
      }
    }
  }

  .lnk {

    .form-group {
      margin: 0 0 20px;

      select {
        max-width: 192px;
        @media (max-width: $screen-xs-max) { // max 767px
          width: 192px;
        }
      }
    }
  }

  .ajax_cart_block_remove_link {
    font-size: 14px;
    line-height: 14px;
    color: #d3d2d2;
    width: 14px;
    height: 14px;
    float: right;

    &:hover {
      color: $link-hover-color;
    }
  }

  .cart_block_product_name {
    width: 85%;
    display: inline-block;
    font-weight: bold;
  }

  .quantity-formated {
    float: left;
    margin: 0 5px 0 0;
    width: 15px;
  }
}

#wishlist_block_list .price {
  float: right;
}

// page in my account
#mywishlist {

  td.wishlist_delete a {
    font-size: 15px;
  }
}

// wishlistLinkTop

#module-blockwishlist-mywishlist {

  #block-order-detail #hideSendWishlist {
    display: inline-block;
  }
}

#module-blockwishlist-mywishlist .wishlistLinkTop ul.display_list {
  border-bottom: 1px solid $base-border-color;
  margin-bottom: 20px;

  a  {
    display: inline-block;
    color: $base-text-color;
    margin: 10px 10px 10px 0;
    font-weight: bold;

    &:hover {
      color: $link-hover-color;
    }
  }
}

#module-blockwishlist-mywishlist .wishlistLinkTop li {
  float: left;
}

#module-blockwishlist-mywishlist .wishlistLinkTop #hideSendWishlist {
  float: right;
}

#module-blockwishlist-mywishlist .wishlistLinkTop #showBoughtProducts,
#module-blockwishlist-mywishlist .wishlistLinkTop #hideBoughtProductsInfos {
  display: none;
}

// wlp_bought_list
ul.wlp_bought_list li {
  margin: 0 0 30px;

  .product_image {
    padding: 9px;
    border: 1px solid $base-border-color;
    margin: 0 0 20px;
    max-width: 270px;
  }

  .product-name {
    margin: 0 0 10px;
    padding: 0 20px 0 0;

    small {
      display: block;
      font-size: 11px;

      a {
        font-size: 11px;
      }
    }
  }

  .product_infos {
    position: relative;
  }

  .btn_action .btn {
    margin: 0 0 10px;
  }

  // >992px
  @media (min-width: $screen-md) {
    &.first-in-line {
      clear: left;
    }
  }

  // 768px x 991px
  @media (min-width: $screen-sm) and (max-width: $screen-sm-max) {
    &.first-item-of-tablet-line  {
      clear: left;
    }
  }
}

.wishlistLinkTop {

  .submit {
    margin: 0 0 30px;
  }
}

ul.wlp_bought_list li .lnkdel {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  font-size: 14px;
}

#wishlist_button > .btn-group {
  width: 80%;
}

ul.product_list.grid > li .product-container .functional-buttons .wishlist .popover {
  padding: 0;
  min-width: 160px;

  .popover-content {
    padding: 0;
    min-width: 158px;
  }

  table {
    margin-bottom: 2px;

    td:hover {
      background-color: rgb(204, 204, 204);
      cursor: pointer;
    }
   }
}

[id='wishlist_button'] + .popover,
.wishlist_change_button + .popover {
  padding: 0;
  min-width: 150px;

  .popover-content {
    padding: 0;
  }

  table {
    margin-bottom: 2px;

    td:hover {
      background-color: rgb(204, 204, 204);
      cursor: pointer;
    }
  }
}
